import { useEffect, useState, useId} from 'react';
import {useLocation,useParams} from 'react-router-dom';
import history from '../utils/history';
import {useSiteContext } from '../utils/context';
import MaroonCard from '../components/MaroonCard';
import { useFormBuilder } from '../utils/forms';
import { IPost} from '../types/blog';
import {isUnAuthorized} from '../pages/UnAuthorized';

const EditPost = () => {
  const context = useSiteContext()
  const location :any = useLocation();
const { pid } = useParams();

  const [state, setState] = useState<{post:IPost | null}>({post:null});

  // check if post in location and set state
  useEffect(() => {
    if (location.state && location.state.post) {
      // check for author is the same
      isUnAuthorized(context.auth?.state.profile?._id !== location.state.post.profile?._id )
      setState({post:location.state.post})
    }else if (!state.post && pid){
    context.ajax?.get('/posts/'+ pid).then(data_res=> data_res.item)
        .then(data => {
          // check for author is the same
          isUnAuthorized(context.auth?.state.profile?._id !== location.state.post.profile?._id )
          data && setState({post:data})}
          ).catch((err) => console.log(err))
    }
  }, [pid])

  useEffect(()=>{

    !context.blog?.categories.state.items && context.getSet!('categories');
    
     },[context.blog?.categories?.state.items,context.getSet])
  
const handleCleanData = (cleanData: any, stateData:any) =>{
  console.log('form clean data :')
  console.log(cleanData)
  console.log('state data :')
  console.log(stateData)
if(state.post)
  return;

  const user_id = context.auth?.state.profile!._id
  const username = context.auth?.state.profile!.username
  //const pid = props.location.state.post.post._id
  const title = cleanData.title
  const body = cleanData.body

  const data = {title: title,
                body: body,
                category: cleanData,
                uid: user_id,
                username: username
               }
               
  context.ajax!.put!('posts/'+state.post!._id, cleanData)
    .then(res => console.log(res))
    .catch(err => console.log(err))
    .then(()=> setTimeout(() => history.replace('/blog/posts'), 700 ))
  
}
const categories = ()=> context.blog?.categories.state.items?.map(cat=> {return {title: cat.name, value: cat._id! }})

const inputs = useFormBuilder({
  elements: [
    {
      lable: "Post Title",
      element: {
          name: "title",
          as:'input',
          type: "text",
          required: true,
          placeholder: "Post title",
          minLength: 3,
          maxLength: 100
      }
    },
    {
      lable: "Select category",
      element: {
          as:'select',
          name: "category",
          required: true 
      },
      selectOptions: {
          lable: "Select subject",
          options: categories()!
        }
    },
    {
      lable: "Post discription",
      element: {
          name: "descriptions",
          required: true,
          placeholder: "Post discription",
          as: "textarea",
          minLength: 3,
          style: { height: '200px' }
      }
    },
    {
      lable: "Post Body",
      element: {
          name: "body",
          required: true,
          placeholder: "Post Body",
          as: "textarea",
          minLength: 3,
          style: { height: '200px' }
      }
    },
    {
      lable: "Is Published .?",
      element: {
          as:'input',
          name: "isPublished",
          type: "checkbox",
          required: false
        }
    },
    {
      lable: "Is Featured .?",
      element: {
          as:'input',
          name: "isfeatured",
          type: "checkbox",
          required: false,
        }
    },
    {
      lable: "Post Image Url",
      element: {
          name: "image",
          as:'input',
          type: "text",
          required: false,
          placeholder: "Image url",
          minLength: 3,
          maxLength: 100
      }
    },
  ],
  isfloating:true,
  form: {
    attrs: {
      id: "editPostForm",
      name: "editPostForm"
    }
  },
  cleandata: handleCleanData,
  getId:useId,
  stateInitials:(()=> { 
    let post:any = {};
    if(location.state && location.state.post){
      post = location.state.post;
      post.category = location.state.post.category._id;
    }else if(state.post) {
      post = state.post!;
      post.category = state.post?.category!._id;
    }
    return post;
  })(),
  addSubmit: true,
  extra: <button type='button' className='btn btn-warning mx-2' onClick={() => history.back()}> Cancel </button>
})
    return(
      state.post ? 
        <MaroonCard title='Edit Post'>
          {inputs}
        </MaroonCard>  : <h4 className='text-danger'> No Post to Edit</h4>
    )}



export default EditPost;
