import {useEffect, useId} from 'react'
import history from '../utils/history';
import { Link } from 'react-router-dom';
import MaroonCard from '../components/MaroonCard'
import { useFormBuilder} from '../utils/forms';
import {useSiteContext } from '../utils/context';

export default function Login() {

const context = useSiteContext()

useEffect(()=> {
  if (context.auth?.state.isAuthenticated) {
    //console.log('auth states profile keys are: '+Object.keys(context.auth?.states!.profile!));
    history.back();
  }else{
    console.log(' authStates === null')
    if(context.auth?.state.loginErrors){
    console.log(' login errors are');
    console.log(context.auth?.state.loginErrors);
    }
  }
},[context.auth?.state])

const elements = useFormBuilder({ 
               
  elements: [
    {
      lable: "Email Address",
      element: {
          as:'input',
          type:'email',
          name: "email",
          required: true,
          placeholder: "Your email ",
          minLength: 3,
          maxLength: 30,
          icon:"bi bi-envelope"
        },
      
    },
    {
      lable: "Password",
      element: {
          as:'input',
          name: "password",
          type: "password",
          required: true,
          placeholder: "Your Password",
          pattern: '^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]',
          minLength: 6,
          maxLength: 11,
          icon:"bi bi-lock"
        }
    },
    {
      lable: "Remember me.?",
      element: {
          as:'input',
          name: "remember",
          type: "checkbox",
          required: false,
        }
    }
  ],
  isfloating:false,
  form: {
    attrs:{
      id: "loginForm",
      name: "loginForm"
    },
    serverErrors:context.auth?.state.loginErrors,
  },
  cleandata: async (clean_Data: any,form_states:any) => {
      console.log(await context.auth!.login(clean_Data.email,clean_Data.password))
    },
  getId:useId,
  addSubmit:true
});
  return (
    <div className="row  fadein">
      <div className="col-md-5 mx-auto ">
        <div className="login-logo">
          <Link to="/login" className='nav-link' ><b>Login</b>Page</Link>
        </div>
        <MaroonCard title='login' >
          <div className="login-card-body ">
            <p className="login-box-msg">Sign in to start your session</p>
            {elements}
            <div className="social-auth-links text-center mb-3">
              <p>- OR -</p>
              <Link to="/" className="btn d-block btn-primary mb-2">
                <i className="bi bi-facebook"></i> Sign in using Facebook

              </Link>
              <Link to="/" className="btn d-block btn-danger">
                <i className="bi bi-facebook"></i> Sign in using Google+
              </Link>
            </div>

            <p className="mb-1">
              <Link className='nav-link text-center ' to="/">I forgot my password</Link>
            </p>
            <p className="mb-0">
              <Link to="/signup" className="text-center nav-link">Create new account</Link>
            </p>
          </div>
     </MaroonCard>
      </div>
    </div>
  )
}
