import * as react from 'react';

interface MaroonCardProps {
    title?:string
    size?:string // xl | lg | md | ''
    textSize?:string // xl | lg | md | sm
    classNames?:string
}
export default function MaroonCard(props:react.PropsWithChildren<MaroonCardProps>){

   return(
    <div className={`card shadow-none card-body card-maroon card-outline  py-1 ${props.classNames!}`}>
        {
            props.title ? 
    <div className={`ribbon-wrapper ribbon-${props.size ?? 'lg'}`}>
                <div className={`ribbon bg-maroon text-${props.textSize ?? 'md'}`}>
                {props.title}
                </div>
            </div>
        : ""
}
      {props.children}

    </div>
   )
}