import React from 'react';
import { GenericInput, Polymorphic, TextProps } from '../components/Polymorphic'
import { getRandom, checkedUp, uuid, validateChanges, useProcessForm } from './html5validation'


export interface FormProps {
    attrs?: React.FormHTMLAttributes<HTMLFormElement>,
    isSubmitting?: boolean,
    serverErrors?: any,
    children?: React.ReactNode;
}

interface FormBuilderElementProps {
    lable: string
    element: TextProps<any>,
    selectOptions?: SelectOptionsProps
}

interface FormBuilderProps {
    isfloating: boolean,
    elements: FormBuilderElementProps[],
    form: FormProps,
    addSubmit?: boolean,
    extra?: JSX.Element,
    getId?(): string,
    stateInitials?: Record<string, string | number | boolean> | any,
    cleandata?(clean:any,states:any): void,
}

interface SelectOptionsProps {
    lable: string,
    options: React.DetailedHTMLProps<React.OptionHTMLAttributes<HTMLOptionElement>, HTMLOptionElement>[],
}


function GenericForm(props: FormProps) {

    let serverValidationErors = props.serverErrors && (props.serverErrors.message ?? String(props.serverErrors));
    return (
        <form   {...props.attrs}  noValidate >
            {serverValidationErors && <div className='form-group'><span className='text-center text-danger'> {serverValidationErors} </span></div>}
            {props.children}
        </form>
    )
}


function useFormBuilder(props: FormBuilderProps) {
    // sortout initial states
    let initialStateObj: Record<string, string | number | boolean> = {};
    if (!props.stateInitials) {
        props.elements.forEach(n => initialStateObj[n.element.name!] = n.element.type && n.element.type === 'checkbox' ? false : "");
    } else {
        //console.log(props.stateInitials)
        initialStateObj = props.stateInitials;
    }

    const { handleSubmit, validated, processChanges, dataState } = useProcessForm(initialStateObj);


    let _elements = props.elements.map((item: FormBuilderElementProps) => {

        let _options = item.selectOptions && (<>
            <option key={props.getId!()} value="" >Select menu</option>
            {item.selectOptions.options.map(o => <option key={props.getId!()} {...o}> {o.title} </option>)}
        </>)

        if (item.element) {
          
            let state: Record<string, string | number | boolean> = {}
            if (item.element.type) {
                item.element.type === 'checkbox' ? state['checked'] = dataState[item.element.name] : state['value'] = dataState[item.element.name];
            }
            else {
                state['value'] = dataState[item.element.name]                
            }

            return <GenericInput  {...state} onChange={processChanges} key={props.getId!()} isfloating={props.isfloating.toString()} lable={item.lable}  {...item.element}  >
                {_options}
            </GenericInput>;
        }
        else {
            throw new Error('empty element no input or select data form formBuilder :' + item.lable)
        }
    })

    return (<form {...props.form.attrs} className={validated ? 'was-validated' : 'needs-validation'} onSubmit={(e: any) => handleSubmit!(e, props.cleandata!)} noValidate>
        {props.form!.serverErrors && <div className='form-group'><span className='text-center text-danger'> {props.form.serverErrors.message ?? String(props.form.serverErrors)} </span></div>}

        {_elements}
        {props.addSubmit && <button disabled={props.form.isSubmitting} type='submit' className='btn btn-primary'>
            {props.form.isSubmitting ? "Loading..." : "submit"} </button>
        }
        {props.extra}
      </form>);

}

export { useFormBuilder, getRandom, GenericForm, GenericInput, checkedUp, validateChanges, useProcessForm, uuid, Polymorphic }