import {useEffect, useId} from 'react';
import history from '../utils/history';
import {useSiteContext } from '../utils/context';
import MaroonCard from '../components/MaroonCard';
import { useFormBuilder } from '../utils/forms';

const AddPost = () => {
  const context = useSiteContext()
  /*
    const handleSubmit = (event:any) => {
      event.preventDefault()
      const user_id = context.dbProfileState[0].uid
      const username = context.dbProfileState[0].username
      const data = {title: event.target.title.value,
                    body: event.target.body.value,
                    username: username,
                    uid: user_id}
  
      axios.post('/api/post/posttodb', data)
        .then(response => console.log(response))
        .catch((err) => console.log(err))
        .then(() => setTimeout(() => history.replace('/'), 700) )
    }
    */


const handleCleanData = (cleanData: any, states:any) =>{
  console.log('form clean data :')
  console.log(cleanData)
  console.log('state data :')
  console.log(states)
if(states)
  return;
  
  const user_id = context.auth?.state.profile!._id
  const username = context.auth?.state.profile!.username
  //const pid = props.location.state.post.post._id
  const title = cleanData.title
  const body = cleanData.body

  const data = {title: title,
                body: body,
                uid: user_id,
                username: username
               }
  context.ajax!.post!('posts/',data)
    .then(res => console.log(res))
    .catch(err => console.log(err))
    .then(()=> setTimeout(() => history.replace('/blog/posts'), 700 ))
}
useEffect(()=>{
  !context.blog?.categories.state.items && context.getSet!('categories');
  
   },[context.blog?.categories?.state.items])
const categories = ()=> context.blog?.categories.state.items?.map(cat=> {return {title: cat.name, value: cat._id! }})


const inputs =useFormBuilder({
  elements: [
    {
      lable: "Post Title",
      element: {
          name: "title",
          as:'input',
          type: "text",
          required: true,
          placeholder: "Post title",
          minLength: 3,
          maxLength: 100
      }
    },
    {
      lable: "Select category",
      element: {
          as:'select',
          name: "category",
          required: true
      },
      selectOptions: {
          lable: "Select subject",
          options: categories()!
        }
    },
    {
      lable: "Post discription",
      element: {
          name: "descriptions",
          required: true,
          placeholder: "Post discription",
          as: "textarea",
          minLength: 3,
          style: { height: '200px' }
      }
    },
    {
      lable: "Post Body",
      element: {
          name: "body",
          required: true,
          placeholder: "Post Body",
          as: "textarea",
          minLength: 3,
          style: { height: '200px' }
      }
    },
    {
      lable: "Is Published .?",
      element: {
          as:'input',
          name: "isPublished",
          type: "checkbox",
          required: false
        }
    },
    {
      lable: "Is Featured .?",
      element: {
          as:'input',
          name: "isfeatured",
          type: "checkbox",
          required: false,
        }
    },
    {
      lable: "Post Image Url",
      element: {
          name: "image",
          as:'input',
          type: "text",
          required: false,
          placeholder: "Image url",
          minLength: 3,
          maxLength: 100
      }
    },
  ],
  isfloating:true,
  form: {
    attrs: {
      id: "addPostForm",
      name: "addPostForm"
    }
  },
  cleandata: handleCleanData,
  getId:useId,
  addSubmit: true,
  extra: <button type='button' className='btn btn-warning mx-2' onClick={() => history.back()}> Cancel </button>
});
    return(
      context.blog?.categories.state.items ? 
        <MaroonCard title='Add Post'>
          {inputs}
        </MaroonCard>  : <h4 className='text-danger'> No Post to Edit</h4>
    )}
       
export default AddPost;
