//import {useEffect, useState} from 'react'
import { NavLink} from "react-router-dom";
import { useSiteContext } from '../utils/context';

export default function Menu() {
  const context = useSiteContext();

  const addActive =(active:{isActive:boolean})=> "nav-link " + (active.isActive === true ? "   rounded-3 p-2 border-top border-bottom border-primary border-2 shadow" : "")
    return (
      
      <nav id="mainNav" className="navbar navbar-expand-lg sticky-top  navbar-light  ">
    <div className="container py-0">
    <NavLink className="navbar-brand border-top  border-3 border-danger  px-2 rounded-3 shadow"  to="/" role="button">
    <span className="h4 ">Khalil <i className="bi bi-person-workspace fs-3 text-success"></i> Tuban</span>
      </NavLink>
      <button className="navbar-toggler collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#navbarColor01" aria-controls="navbarColor01" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="navbar-collapse collapse" id="navbarColor01">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <NavLink  className={addActive} to="/">Home</NavLink >
          </li>
          <li className="nav-item">
            <NavLink  className={addActive} to="blog/posts">Blog</NavLink >
          </li>
          {context.auth?.state.isAuthenticated ? <li className="nav-item"><NavLink  className={addActive} to="blog/addpost">add post</NavLink ></li> :"" }
          <li className="nav-item">
            <NavLink  className={addActive} to="portfolio">Portfolio</NavLink >
          </li>
          <li className="nav-item">
            <NavLink  className={addActive} to="contact">Contact</NavLink >
          </li>
        </ul>
        <div className="d-flex" role="search">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
           {!context.auth?.state.isAuthenticated ? <NavLink  className={addActive} to="login">Login <i className="bi bi-box-arrow-in-left text-dark"></i> </NavLink > :<NavLink  className="nav-link" onClick={() => context.auth!.logout()} to="/"><i className="bi bi-box-arrow-right text-warnimg"></i> Logout</NavLink > }
          </li>
        </ul>
        </div>
      </div>
    </div>
  </nav>
    );
}
