import React,{ useId} from 'react';
import { Row, Col } from 'react-bootstrap';
import { useFormBuilder } from '../utils/forms';
import MaroonCard from '../components/MaroonCard';



export default function Contactme() {

  const elements = useFormBuilder({ 
               
    elements: [
      {
        lable: "Select subject",
        element: {
            as:'select',
            name: "subject",
            required: true 
            
        },
        selectOptions: {
            lable: "Select subject",
            options: [
              { title: "consultation", value: "1" },
              { title: "Question", value: "2" },
              { title: "Hire me", value: "3" },
              { title: "Contact", value: "4" }
            ]
          }
      },
      {
        lable: "Full Name",
        element: {
            as:'input',
            name: "fullname",
            type: "text",
            required: true,
            placeholder: "Your Name",
            pattern: '^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]',
            minLength: 3,
            maxLength: 30,
          }
      },
      {
        lable: "Email Address",
        element: {
            as:'input',
            type:'email',
            name: "email",
            required: true,
            placeholder: "Your email",
            minLength: 3,
            maxLength: 30
          }
        
      },
      {
        lable: "Telphone Number",
        element:{
            as:'input',
            name: "telphone",
            type: "tel",
            placeholder: "Your phone number",
            pattern: '[+0-9]{11,15}',
          }
        
      },
      {
        lable: "Your Message",
        element: {
            name: "message",
            required: true,
            placeholder: "Your message",
            as:"textarea",
            minLength: 3,
            style: { height: '100px' }
          }                   
      },
      {
        lable: "Shall we call you .?",
        element: {
            as:'input',
            name: "terms",
            type: "checkbox",
            required: true,
          }
      }
    ],
    isfloating:true,
    form: {
      attrs:{
        id: "contactForm",
        name: "contactForm",
      }
    },
    cleandata: (clean_form_Data: any,form_data_states:any) => {
      console.log('clean data begin .......... :')
    console.log(clean_form_Data)
    console.log('clean data end .......... :')
      console.log('data states begin .......... :')
    console.log(form_data_states)
    console.log('data states end .......... :')
    },
    getId:useId,
    addSubmit:true
  });

  return (

    <Row className="justify-content-md-center fadein">
      <Col md="6" className="mx-auto">
        <MaroonCard title='Contact'>
        <h4 className='text-center'>Please use this form</h4>

          <hr className='py-2 bg-warning'/>
         
            {elements}
              
        </MaroonCard>
      </Col>
    </Row>
  

  );
}

class ErrorBoundary extends React.Component {
  constructor(props:any) {
    super(props);
    this.state = { hasError: false,  error: null, errorInfo: null };
  }
  state: Readonly<{ hasError: boolean, error: any, errorInfo: any }>;
  props:any
  static getDerivedStateFromError(error:any) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error:any, errorInfo:any) {
    // Catch errors in any components below and re-render with error message
    this.setState({
      hasError:true,
      error: error,
      errorInfo: errorInfo
    })
    // You can also log error messages to an error reporting service here
  }

  render() {
    if (this.state.errorInfo) {
      // Error path
      return (
        <div>
          <h2 className='text-danger'>Something went wrong.</h2>
          <p className='text-danger'>
            {this.state.error && this.state.error.toString()}
            <hr />
            {this.state.errorInfo.componentStack}
          </p>
        </div>
      );
    }
    // Normally, just render children
    return this.props.children;
  }  
}


